import styled from "styled-components"
import Check_succes from "@assets/images/premium/cards/check_success.svg"
import Check_date from "@assets/images/premium/cards/check_date.svg"
import Check_dollar from "@assets/images/premium/cards/check_dollar.svg"
import Calendar from "@assets/images/premium/cards/Calender.svg"
import users from "@assets/images/premium/cards/users.svg"
import Before_radius from "@assets/images/premium/cards/before_radius.svg"
export const Wrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 70px;
 .cards{
    width: 29.3%;
    background: #FFFFFF;
    border-radius: 12px;
    box-shadow: 0px 5px 23px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    padding: 20px 0px 37px 30px;
    height: 210px;
    }
    h3{
    margin: 0;
    padding: 0;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    color: #000000;
    display: flex;
    margin-left: 5px; 
    margin-bottom:23px ;
    align-items: center;
    }
    .cards:nth-child(1) h3::before{
        content: "";
        display: flex;
        background-image: url(${Check_succes});
        width: 28px;
        height: 28px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
    .cards:nth-child(2) h3::before{
        content: "";
        display: flex;
        background-image: url(${Check_date});
        width: 28px;
        height: 28px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
    .cards:nth-child(3) h3::before{
        content: "";
        display: flex;
        background-image: url(${Check_dollar});
        width: 28px;
        height: 28px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
    h4{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        color: #06D6A0;
        margin-bottom:23px ;
    }
    .cards:nth-child(3) h4{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 32px;
        line-height: 39px;
        color: #06D6A0;
    }
    h2{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        opacity: 0.6;
        display: flex;
        margin-left: 5px;
        margin-bottom: 13px;
        width: 255px;
    }
    .cards:nth-child(1) h2::before{
        content: "";
        display: flex;
        background-image: url(${Calendar});
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
    .cards:nth-child(2) h2::before{
        content: "";
        display: flex;
        background-image: url(${Calendar});
        width: 25px;
        height: 24px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
     h5{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        opacity: 0.6;
        display: flex;
        margin-left: 5px;
    }
    .cards:nth-child(1)::after{
        content: "";
        display: flex;
        background-image: url(${Before_radius});
        width: 110px;
        height: 90px;
        background-repeat: no-repeat;
        border-bottom-right-radius: 12.6px;
        margin-top: -56.8px;
        margin-left: auto;
    }
    .cards:nth-child(3)::after{
        content: "";
        display: flex;
        background-image: url(${Before_radius});
        width: 110px;
        height: 90px;
        background-repeat: no-repeat;
        border-bottom-right-radius: 12.6px;
        margin-top: -25px;
        margin-left: auto;
    }
    .cards:nth-child(2)::after{
        content: "";
        display: flex;
        background-image: url(${Before_radius});
        width: 110px;
        height: 90px;
        background-repeat: no-repeat;
        border-bottom-right-radius: 12.6px;
        margin-top: -71px;
        margin-left: auto;
    }
    .cards:nth-child(1) h5::before{
        content: "";
        display: flex;
        background-image: url(${users});
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
    .cards:nth-child(2) h5::before{
        content: "";
        display: flex;
        background-image: url(${users});
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        position: relative;
        right: 5px;
    }
`