import { FC, useState } from "react";
import { PremiumProps } from "./types-premium";
import PremiumTop from "./premium-top/premium-top";
import PremiumCard from "./premium-cards/premium-cards";
import PremiumPlan from "./premium-plan/premium-plan";
import PremiumFill from "./premium-fill/premium-fill";
import PremiumTable from "./premium-table/premium-table";
import PremiumUsers from "./premium-users/premium-users";
import { Wrapper } from "./styles-premium";

const Premium: FC<PremiumProps> = () => {
  const [value, setValue] = useState<string>("")

  return (
    <Wrapper>
      <PremiumTop />
      <PremiumCard />
      <div className="premium-center">
        <PremiumPlan />
        <PremiumFill />
      </div>
      <div className="premium-center">
        <PremiumTable selectedValue={setValue}/>
        <PremiumUsers />
      </div>
    </Wrapper>
  );
};

export default Premium;
