import { FC  , useState} from "react";
import { Wrapper } from "./style-premium-plan-form";
import { PremiumPlanFormProps , OpenPlanForm } from "./types-premium-form";
import { UniversalModal } from "@components/common";
import { useForm } from "react-hook-form";
import Input from "@components/common/input/input";
import {Button} from "@components/common";
import {PhoneInput} from "@components/common";
const PremiumPlanForm: FC<PremiumPlanFormProps> = ({open, handleClose}) => {
    const { handleSubmit, control } = useForm<OpenPlanForm>({
        defaultValues: {
          CompanyName: "",
          Number : 0,
          email : "",
        },
      });
  return <UniversalModal  open={open} handleClose={handleClose}>
  <Wrapper>
    <h2>Sotib olish uchun ariza qoldiring</h2>
    <Input title="Kompaniya nomi"            
            onChange={() => console.log("clicked")}
            name="CompanyName"
            control={control} 
            placeholder="Kompaniya nomini kiriting"  />
        <PhoneInput            
            name=" Number"
            control={control} 
             />
    <Input title="E-mail"            
            onChange={() => console.log("clicked")}
            name="email"
            control={control} 
            placeholder="E-mail kiriting"  />

     <Button type="button" value="Ariza qoldirish"/>
  </Wrapper> 
</UniversalModal>
};

export default PremiumPlanForm;