import { FC } from "react";
import { PremiumTableCalendarProps  } from "./type-premium-table-calendar";
import { Wrapper  } from "./style-premium-table-calendar";
import {UniversalModal} from '@components/common';
import {Calendar} from '@components/common';
const  PremiumTableCalendar: FC<PremiumTableCalendarProps> = ({open, handleClose})=>{
    return <UniversalModal  open={open} handleClose={handleClose}>
    <Wrapper>
      <Calendar/>
    </Wrapper> 
  </UniversalModal>
}
export default PremiumTableCalendar