import styled from "styled-components"
 
export const Wrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 38px;
    align-items: center;
    h2{
        margin: 0;
        padding: 0  ;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        color: #000000;   
 }
    .heading_center_button{

    }
    Button::before{
        content: "+";
        display: flex;
        font-size: 20px;
        color: white;
        font-weight: bold;
        position: relative;
        right: 5px;
    }
    Button{
        cursor: pointer;
        margin: 0;
        padding: 0;
        background: #0496FF;
        border: 1px solid #0496FF;
        border-radius: 60px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #FFFFFF;
        display: flex;
        align-items: center;
        color: #FFFFFF;
        padding: 12px 20px;
        display: flex;
        margin-left: 5px;
        width: 100%;
    }
    .fRHyDX{
        width: 221px;
    }
`
export const CenterTop = styled.div`
    span{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #000000;
        opacity: 0.6;
    }
    span:nth-child(2){
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #0496FF;
        margin-left: 19px;
    }
`