import styled from "styled-components";
 
export const Wrapper  = styled.div`
    padding: 20px;
    h2{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 34px;
        color: #000000;
    }
    .premium_form_btn{
        width: 100%;
        padding: 18px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        background: #0496FF;
        border-radius: 7px;
        margin-top: 30px;
        border: transparent;
    }
    .iLJagp input{
        background-color: white !important;
    }
    .react-tel-input .flag-dropdown{
        background-color: white;
    }
    .react-tel-input .form-control{
        height: 49px !important;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }
    .react-tel-input .flag-dropdown{
        height: 49px;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }
` 