import styled from "styled-components";

export const Wrapper  = styled.div`
    width: 40%;
    h2{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        color: #000000;
        margin-bottom: 20px;
    }
    h3:nth-child(1){
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #333333;
    }
    h4{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
    }
    .liciZb{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        color: #000000;
        opacity: 0.6;
    }
    .bkrbaw{
        margin-right: auto;
        width: 55%;
    }
    .fill_img{
    width: 120px;
    height: 70px;
    padding: 20px 20px;
    background: #FFFFFF;
    border: 1px solid grey;
    border-radius: 5px;
    margin-top: 15px;
}
    .fill_heading2{
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
        }
        .img_valute{
            cursor: pointer;
            position: relative;
            bottom: -60px;
        }
    .valute{
        width: 100%;
        display: flex;
        justify-content: space-between
    }
    .valute_left{
        margin: 0;
        padding: 0;
        width: 40%;
        img{
            display: flex;
            margin-left: auto;
            position: relative;
            top: 60px;
            right: 10px;
            z-index: 1000;
        }
    }
    .jPCtTr{
        margin: 0px;
    }
    .valute_right{
        width: 40%;
        img{
            display: flex;
            margin-left: auto;
            position: relative;
            top: 60px;
            right: 10px;
            z-index: 1000;
        }
    }
    .valute_button{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        background: #0496FF;
        border-radius: 7px;
        padding: 18px 60px;
        border: transparent;
        margin-top: 20px;
        cursor: pointer;
    }
    
` 
