import { FC } from "react";
import { PremiumCardProps } from "./types-premium-card";
import { Wrapper } from "./styles-premium-card"

const CardData = [
  {
    id : 1 ,
    title: "Aktive",
    description : "Demo versiya",
    data : "2022-05-01 gacha",
    users : "1000 ta xodim uchun"
  },
  {
    id : 2 ,
    title: "Aktivlashtirlamagan",
    description : "Navbatdagi paket",
    data : "2022-05-01 dan  2022.05.01 gacha",
    users : "1000 ta xodim uchun"
  },
  {
    id : 3 ,
    title: "Aktivlashtirlamagan",
    description : "$ 12000",
  },
]

const PremiumCard: FC<PremiumCardProps> = () => {
    return <Wrapper>
        {CardData.map(elem =>(
          <>
        <div className="cards" key={elem.id}>
            <h3>{elem.title}</h3>
            <h4>{elem.description}</h4>
            <h2>{elem.data}</h2>
            <h5>{elem.users}</h5>
        </div>
          </>
        ))}
      </Wrapper>;
  };
  
  export default PremiumCard ;