import styled from "styled-components"
 
export const Wrapper = styled.div`
    padding: 30px;
    h2{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        color: #000000;
        margin-bottom: 18px;
    }
    h3{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        color: #000000;
        margin-bottom: 18px;
    }
    .css-1aznpnh-MuiSlider-root{
        height: 15px;
        width: 87%;
    }
    .css-eg0mwd-MuiSlider-thumb{
        width: 24px;
        height: 24px;
        border: 2px solid #0496FF;
        background: white;
    }
    .css-1tfve6q-MuiSlider-mark{
        display: none;
    }
    .css-6cwnna-MuiSlider-mark{
        display: none;
    }
    .css-1eoe787-MuiSlider-markLabel{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #000000;

    }
    Button{
        background: #0496FF;
        border-radius: 7px;
        padding: 18px 60px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
        border: transparent;
        margin-top: 30px;
        cursor: pointer;
    }
`
export const Bottom = styled.div`
        span:nth-child(1){
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #A5A9B8;
            margin-right: 10px;
        }
        span:nth-child(2){
            font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        color: #06D6A0;
        }
`
export const Center = styled.div`
        margin-bottom: 16px;
        span:nth-child(1){
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #A5A9B8;
            margin-right: 10px;
        }
        span:nth-child(2){
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
        }
`