import { FC  , useState} from "react";
import { PremiumPlanProps } from "./type-premiun-plan";
import { Wrapper, Boxs , Center  ,ModalCenter , SpanGray , Span ,SpanGreen , Success} from "./styles-premium-plan";
import * as React from 'react';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import PremiumModalSuccess from "./premium-plan-sucess-modal/premium-plan-success-modal";
import PremiumPlanForm from "./premium-plan-form-modal/premium-plan-form";
import { Button } from "@components/common";
import {UniversalModal} from "@components/common";
import Check_success from "@assets/images/premium/plan/check_success.svg"
const PremiumPlan: FC<PremiumPlanProps> = () => {
  const [activeModal , setActiveModal] = useState<number>(0);
  const ChangeModal = (activeModal : number) =>{
    setActiveModal(activeModal)
    setOpen(true);
  }
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen = () => {
    setOpen(true);
  };
  const [open2, setOpen2] = useState(false);
  const handleClose2 = () => {
    setOpen2(false);
  };
  const handleOpen2 = () => {
    setOpen2(true);
  };
    const marks  = [
        {
          value: 5,
          label: '5',
        },
        {
          value: 15,
          label: '15',
        },
        {
          value: 25,
          label: '25',
        },
        {
          value: 40,
          label: '40',
        },
        {
          value: 60,
          label: '60',
        },
        {
          value: 80,
          label: '90',
        },
        {
          value: 98,
          label: '100',
        },
      ];
      const marks2  = [
        {
          value: 23,
          label: '2 yil',
        },
        {
          value: 17,
          label: '1-yil',
        },
        {
          value: 11,
          label: '6-oy',
        },
        {
          value: 5,
          label: '3-oy',
        },
      ];
  return <Wrapper>
    <h3>Tarif planlar</h3>
    <Boxs>
        <h4>Xodimlar sonini tanlang</h4>
        <Box sx={{ width: 300 }}>
      <Slider
        aria-label="Custom marks"
        step={null}
        defaultValue={0}
        max={100}
        marks={marks}
        min={5}
        aria-labelledby="non-linear-slider"
      />
    </Box>
      <Center>
      <div className="Left_plan">
    <h2>Ishlash muddatini tanlang</h2>
      <Box sx={{ width: 300 }}>
     <Slider aria-label="Custom marks" defaultValue={24} step={null} max={24} min={3} marks={marks2}  aria-labelledby="non-linear-slider"/>
    </Box>
      </div>
        <div className="Right_plan">
        <h3>Kvitansiya:</h3>
        <ModalCenter>
        <SpanGray>Xodimlar soni limiti:</SpanGray>
        <Span>5</Span>
      </ModalCenter>
      <ModalCenter>
        <SpanGray>Muddat (oy):</SpanGray>
        <Span>24 oy</Span>
      </ModalCenter>
      <div className="Modal_premium_bottom">
      <SpanGray>Umumiy:</SpanGray>
      <SpanGreen>$1200</SpanGreen>
      </div>
        </div>
      </Center>
      <div className="buttons">
        <Button type="button"  value="Tasdiqlash" onClick={() => ChangeModal(1)}/>
        <Button type="button" color="#212121"  backColor="#FFBD26"  value="Doimiy muddatga sotib olish" onClick={handleOpen2}/>
      </div>
    </Boxs>
    <UniversalModal  open={open} handleClose={handleClose}>
      {activeModal === 1 ?(
    <PremiumModalSuccess changeModal={ChangeModal}/>
      ): activeModal === 2 ?(
          <Success>
            <img src={Check_success} width={71} height={71} alt={Check_success} />
            <span>Faolashtirish muaffaqiyatli yakulandi</span>
            <Button type="button" onClick={handleClose} value="OK"  />
        </Success>
      ): null}
    </UniversalModal>
    <PremiumPlanForm open={open2} handleClose={handleClose2}/>
  </Wrapper>;
};

export default PremiumPlan;