import styled from "styled-components";
import Users from "@assets/images/premium/users/Search.svg";
export const Wrapper = styled.div`
  margin-top: 63px;
  width: 30%;
  .user_top {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #f4f5f7;
  }
  h3 {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    margin-bottom: 29px;
  }
  span:nth-child(1) {
    font-family: "Inter";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
  }
  span:nth-child(2) {
    font-family: "Inter";
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #06d6a0;
  }
  .users {
    overflow: scroll;
    height: 355px;
    margin-top: 20px;
  }
  .user_search {
    background-color: white;
    border: 2px solid #d3d3d3;
    border-radius: 7px;
    padding: 15px;
    width: 100%;
    margin-top: 20px;
  }
  .search::before {
    content: "";
    display: flex;
    background-image: url(${Users});
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    margin-left: auto;
    z-index: 1000;
    position: relative;
    top: 55px;
    right: 20px;
  }
  .modal_padding{
    position: relative;
    background-color: red;
  }
  .user {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 29px;
    margin-bottom: 25px;
  }
  .css-1yjjitx-MuiSwitch-track {
    width: 82px;
    height: 22px;
    border-radius: 16px;
    background: gray;
  }
  .css-julti5-MuiSwitch-root {
    padding: 8px 12px;
  }
  .css-jsexje-MuiSwitch-thumb {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-left: 10px;
  }
  .css-jsexje-MuiSwitch-thumb {
    margin-left: 0px !important;
  }
  h5 {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
  }
  .css-jsexje-MuiSwitch-thumb {
    background-color: white;
  }
  .css-5ryogn-MuiButtonBase-root-MuiSwitch-switchBase.Mui-checked
    + .MuiSwitch-track {
    opacity: 1 !important;
    background-color: #0496ff;
  }
  h4 {
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }
`;
