import styled from "styled-components";

export const Wrapper = styled.div`
    padding: 35px;
    h3{
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 34px;
        text-align: center;
        color: #000000;
        width: 426px;
    }
    .gUlmHx{
        border: 1px solid rgba(0, 0, 0, 0.4);
        border-radius: 7px;
    }
    .left_button{
        padding: 13px 30px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        color: rgba(0, 0, 0, 0.4);
        background: #FFFFFF;
        border: 1px solid rgba(0, 0, 0, 0.4);
        border-radius: 7px;
        cursor: pointer;
    }
    .buttons{
        width: 100%;
        display: flex;
        justify-content: space-around;
        margin-top: 30px;
    }
    .right_button{
        cursor: pointer;
        background: #0496FF;
        border-radius: 7px;
        padding: 13px 30px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        color: #FFFFFF;
        border: transparent;
    }
`   
export const Img = styled.img`
        width: 100%;
        margin: 0 auto;
        margin-bottom: 40px;
`