import * as React from 'react';
import { FC } from "react";
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import { PremiumTopModalProps } from "./types-premium-top-modal";
import { Wrapper ,Center,Bottom } from "./styles-premium-top-modal"
import { UniversalModal } from "@components/common";
import {Button} from '@components/common';
const PremiumModalTop: FC<PremiumTopModalProps> = ({open, handleClose}) => {
  const marks  = [
    {
      value: 23,
      label: '2 yil',
    },
    {
      value: 17,
      label: '1-yil',
    },
    {
      value: 11,
      label: '6-oy',
    },
    {
      value: 5,
      label: '3-oy',
    },
  ];
    return <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
      <h2>Ishlash muddatini tanlang</h2>
      <Box sx={{ width: 300 }}>
                   <Slider
                            aria-label="Custom marks"
                            defaultValue={24}
                            step={null}
                            max={24}
                            min={3}
                            marks={marks}
                            aria-labelledby="non-linear-slider"
                            />
    </Box>
      <h3>Kvitansiya:</h3>
      <Center>
        <span>Muddat (oy):</span>
        <span >24 oy</span>
      </Center>
      <Bottom >
      <span>Umumiy:</span>
      <span>$1200</span>
      </Bottom>
      <Button  type="button" value="Saqlash"/>
      </Wrapper>
    </UniversalModal>
  };
  
  export default PremiumModalTop;